import React from 'react';
import {Jumbotron, Button, Card} from "react-bootstrap";
import {useHistory, Link} from 'react-router-dom';
import '../SCSS/reset.scss';

function Gacha(props) {

    return (
        <div>

            <Card className="text-center">
                <Card.Header>Gacha</Card.Header>
                <Card.Body>
                    <Card.Title>Let's Gacha!</Card.Title>
                    <Card.Text>
                        가챠에 도전해 LuckyBOX를 얻으세요!
                    </Card.Text>
                    <Button variant="success" as={Link} to="/id" onClick={() => {}}>
                        가챠 도전</Button>
                    <Button variant="success" as={Link} to="/cart" onClick={() => {}}>
                        장바구니</Button>
                </Card.Body>
                <Card.Footer className="text-muted">준비중입니다.</Card.Footer>
            </Card>

            <Jumbotron className="Gacha_background">

                <div id="space"></div>
            </Jumbotron>

        </div>
    )
}

export default Gacha;