/*eslint-disable*/
import React, {useContext, useState} from 'react';
import './CSS/App.css';
import Data from './data.js';
import Detail from './Components/Detail.js'
import MainPage from './Components/MainPage.js'
import 'bootstrap/dist/css/bootstrap.min.css';
import {Navbar, Container, Nav, NavDropdown} from "react-bootstrap";
import axios from 'axios';
import Cart from './Components/Cart.js'
import Gacha from './Components/Gacha.js'

import {Link, Route, Switch} from 'react-router-dom';
import {useHistory} from 'react-router-dom';

export let 재고context = React.createContext();

function App() {

    let [shoes, shoes변경] = useState(Data);
    let [재고, 재고변경] = useState([1, 2, 3]);

    return (
        <div className="App">
            <Navbar bg="light" expand="lg">
                <Container>
                    <Navbar.Brand href="#/">Let's Draw!</Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav"/>
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="me-auto">
                            <Nav.Link as={Link} to="/">
                                Home
                            </Nav.Link>
                            {/* <Nav.Link as={Link} to="/detail/0">Detail</Nav.Link> */}
                            <Nav.Link as={Link} to="/id">Trading</Nav.Link>
                            <NavDropdown title="Item" id="basic-nav-dropdown">
                                <NavDropdown.Item as={Link} to="/detail/0">Shoes</NavDropdown.Item>
                                <NavDropdown.Item as={Link} to="/id">Cloths</NavDropdown.Item>
                                <NavDropdown.Item as={Link} to="/gacha">Gacha</NavDropdown.Item>
                                <NavDropdown.Divider/>
                                <NavDropdown.Item as={Link} to="/cart">Cart</NavDropdown.Item>
                            </NavDropdown>
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>

            <Switch>
                <Route exact="exact" path='/'>
                    <MainPage/>
                    <div className="container">

                        <재고context.Provider value={재고}>

                            <div className="row">
                                {
                                    shoes.map((a, i) => {
                                        return <Card shoes={shoes[i]} i={i} key={i}/>
                                    })
                                }
                            </div>
                        </재고context.Provider>

                        <button
                            className="btn btn-primary"
                            onClick={() => {
                                // axios.post('서버URL', (id: 'id', pw:'1234')).then(()=>{})
                                axios
                                    .get('https://codingapple1.github.io/shop/data2.json')
                                    .then((result) => {
                                        console.log(result.data);
                                        shoes변경([
                                            ...shoes,
                                            ...result.data
                                        ]);
                                    })
                                    .catch(() => {
                                        console.log('실패했어요')
                                    })

                                }}>더보기</button>
                    </div>
                </Route>

                <Route path='/detail/:id'>
                    <재고context.Provider value={재고}>
                        <Detail shoes={shoes} 재고={재고} 재고변경={재고변경}/>
                    </재고context.Provider>
                </Route>

                <Route path="/gacha">
                    <Gacha></Gacha>
                </Route>

                <Route path="/cart">
                    <Cart></Cart>
                </Route>

                <Route path='/:id'>
                    <div>아직 준비안된 페이지</div>
                </Route>

            </Switch>
        </div>
    )

}

function Card(props) {

    let 재고 = useContext(재고context);
    let history = useHistory();

    return (
        <div
            className='col-md-4'
            onClick={() => {
                history.push('/detail/' + props.shoes.id)
            }}>
            <img
                src={'https://codingapple1.github.io/shop/shoes' + (
                    props.i + 1
                ) + '.jpg'}
                width="100%"/>
            <h4>{props.shoes.title}</h4>
            <p>{props.shoes.content}</p>
            <p>{props.shoes.price}원</p>
            <Test></Test>
        </div>
    )
}

function Test() {
    let 재고 = useContext(재고context);
    return <p>응모 가능 횟수: {재고[0]}</p>
}

export default App;
